<template>
  <div class="content">
    <div class="skill_top">
      <div class="label">
        <span class="dot" style="background:#e63939"></span>
        <span class="text" style="color:#e63939">精通</span>
      </div>
      <div class="label">
        <span class="dot" style="background:#cf76f3"></span>
        <span class="text" style="color:#cf76f3">熟练</span>
      </div>
      <div class="label">
        <span class="dot" style="background:#6f77ee"></span>
        <span class="text" style="color:#6f77ee">掌握</span>
      </div>
      <div class="label">
        <span class="dot" style="background:#7eee6f"></span>
        <span class="text" style="color:#7eee6f">了解</span>
      </div>
    </div>
    <div class="label_box">
      <div
        class="labels"
        v-for="(i,index) in list"
        :key="index"
        :style="`background:${color(i.type)};animation-delay:${index*0.1}s;`"
      >
        <span>{{i.title}}</span>
      </div>
    </div>
    <text-bottom :block="true"></text-bottom>
  </div>
</template>

<script>
import TextBottom from "../common/TextBottom";
export default {
  components: {
    "text-bottom": TextBottom
  },
  data() {
    return {
      list: [
        {
          title: "css+html",
          type: "1"
        },
        {
          title: "vue.js",
          type: "2"
        },
        {
          title: "javascript",
          type: "2"
        },
        {
          title: "svg",
          type: "2"
        },
        {
          title: "anime.js",
          type: "2"
        },
        {
          title: "element-ui",
          type: "2"
        },
        {
          title: "mint-ui",
          type: "2"
        },
        {
          title: "vue-cli脚手架",
          type: "2"
        },
        {
          title: "Git",
          type: "3"
        },
        {
          title: "webpack打包工具",
          type: "3"
        },
        {
          title: "canvas",
          type: "3"
        },
        {
          title: "swiper.js",
          type: "3"
        },
        {
          title: "微信小程序",
          type: "3"
        },
        {
          title: "jquery",
          type: "3"
        },
        {
          title: "mock.js",
          type: "3"
        },
        {
          title: "PS",
          type: "3"
        },
        {
          title: "HTTP协议",
          type: "3"
        },
        {
            title:"webSocket",
            type: "3"
        },
        {
            title:"Echarts",
            type: "3"
        },
        {
          title: "bootstrap",
          type: "4"
        },
        {
          title: "MySQL",
          type: "4"
        },
        {
          title: "MongoDB",
          type: "4"
        },
        {
          title: "uni-app",
          type: "4"
        },
        {
          title: "three.js",
          type: "4"
        },
        {
          title: "Tweenlite",
          type: "4"
        },
        {
          title: "node.js",
          type: "4"
        }
      ]
    };
  },
  methods: {
    color(type) {
      if (type == "1") {
        return "#e63939";
      } else if (type == "2") {
        return "#cf76f3";
      } else if (type == "3") {
        return "#6f77ee";
      } else if (type == "4") {
        return "#7eee6f";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  padding: 1rem;
}
.skill_top {
  display: flex;
  justify-content: center;
  .label {
    margin-right: 1rem;
    display: flex;
    align-items: center;
    font-size: 0.75rem;
    .dot {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 50%, 0 100%);
      display: inline-block;
      width: 10px;
      height: 10px;
      // background: #7eee6f;
      margin-right: 0.5rem;
    }
  }
}
.label_box {
  margin-top: 2rem;
  display: flex;
  flex-wrap: wrap;
  filter: drop-shadow(2px 2px 2px #a1a1a1);
  .labels {
    transition: 0.3s;
    animation: textMove 0.5s ease both 1;
    color: white;
    font-size: 0.88rem;
    padding: 0.3rem 1.5rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
    position: relative;
    display: flex;
    align-items: center;
    clip-path: polygon(
      0 50%,
      10px 0,
      100% 0,
      calc(100% - 10px) 50%,
      100% 100%,
      10px 100%
    );
    &:hover {
      clip-path: polygon(0 50%, 10px 0, 100% 0, 100% 50%, 100% 100%, 10px 100%);
    }
    &::before {
      content: "";
      width: 6px;
      height: 6px;
      background: #f1f1f1;
      position: absolute;
      left: 0.7rem;
      top: 40%;
      box-shadow: 1px 1px 2px #a1a1a1 inset;
    }
  }
}
@keyframes textMove {
  0% {
    opacity: 0;
    transform: scale(1.2);
  }
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
</style>


